html {
  scroll-behavior: smooth;
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

input, select, button, textarea {
  font: inherit;
}

img, svg, video, picture {
  display: block;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

body {
  min-height: 100vh;
}

h1, h2 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 0%);
}

h1 {
  font-size: 2.5rem;
  line-height: 2.375rem;
}
@media (min-width: 40rem) {
  h1 {
    font-size: 4.5rem;
    line-height: 4.375rem;
  }
}

h2 {
  font-size: 2rem;
  line-height: 2rem;
}
@media (min-width: 40rem) {
  h2 {
    font-size: 3rem;
    line-height: 3rem;
  }
}

body {
  font-family: "Alata", sans-serif;
  color: hsl(0deg, 0%, 41%);
  font-size: 0.9375rem;
}

a {
  text-decoration: none;
  color: hsl(0deg, 0%, 100%);
}

.container {
  max-width: 70.3125rem;
  margin: 0 auto;
  padding-inline: 1.5625rem;
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding-top: 2.5rem;
  z-index: 1;
}
@media (min-width: 40rem) {
  .header {
    padding-top: 4rem;
  }
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav-brand {
  width: 9rem;
  height: 1.5rem;
}
@media (min-width: 40rem) {
  .header__nav-brand {
    width: 12rem;
    height: 2rem;
  }
}
.header__nav-toggle {
  all: unset;
}
@media (min-width: 40rem) {
  .header__nav-toggle {
    display: none;
  }
}
.header__nav-menu {
  display: none;
}
@media (min-width: 40rem) {
  .header__nav-menu {
    display: block;
    position: static;
  }
}
.header__nav-items {
  font-family: "Josefin Sans", sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
@media (min-width: 40rem) {
  .header__nav-items {
    font-family: "Alata", sans-serif;
    text-transform: capitalize;
    font-size: 0.9375rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
}
.header__nav-link {
  position: relative;
}
@media (min-width: 40rem) {
  .header__nav-link::after {
    content: "";
    visibility: hidden;
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 1.5rem;
    height: 0.125rem;
    background-color: hsl(0deg, 0%, 100%);
    transition: all 150ms ease-in-out;
  }
  .header__nav-link:hover::after {
    visibility: visible;
  }
}
.header__overlay {
  display: none;
}
.header.open .header__nav-menu {
  display: block;
  position: absolute;
  left: 1.5rem;
  top: 14.5625rem;
}
.header.open .header__overlay {
  display: block;
  position: absolute;
  inset: 0;
  background-color: hsl(0deg, 0%, 0%);
  z-index: -1;
}
@media (min-width: 40rem) {
  .header.open .header__nav-menu {
    position: static;
    display: block;
  }
  .header.open .header__overlay {
    display: none;
  }
}

.hero {
  min-height: 100vh;
  background-image: url(/images/mobile/image-hero.jpg);
  background-size: cover;
  background-position: center top;
  padding-top: 14.1875rem;
}
@media (min-width: 40rem) {
  .hero {
    background-image: url(/images/desktop/image-hero.jpg);
  }
}
.hero__text {
  padding: 1.5rem;
  border: 0.125rem solid hsl(0deg, 0%, 100%);
}
@media (min-width: 40rem) {
  .hero__text {
    max-width: 40.625rem;
    padding: 2.5rem;
  }
}
.hero__title {
  color: hsl(0deg, 0%, 100%);
}

.info {
  padding-block: 6rem;
  position: relative;
}
@media (min-width: 62.5rem) {
  .info {
    padding-top: 10rem;
    padding-bottom: 11.5rem;
  }
}
.info__picture {
  margin-bottom: 3rem;
  max-width: 45.6875rem;
}
@media (min-width: 62.5rem) {
  .info__picture {
    margin-bottom: 0;
  }
}
.info__text {
  text-align: center;
}
@media (min-width: 62.5rem) {
  .info__text {
    position: absolute;
    right: 0;
    bottom: 11.5rem;
    width: 33.75rem;
    height: fit-content;
    padding-top: 6rem;
    padding-left: 6rem;
    background-color: hsl(0deg, 0%, 100%);
    text-align: left;
  }
}
.info__title {
  margin-bottom: 1rem;
}

.creations {
  text-align: center;
  margin-bottom: 6rem;
}
@media (min-width: 62.5rem) {
  .creations {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-areas: "title button" "cards cards";
    row-gap: 5.25rem;
    text-align: left;
    margin-bottom: 11.5rem;
  }
}
.creations__title {
  margin-bottom: 3rem;
}
@media (min-width: 62.5rem) {
  .creations__title {
    margin-bottom: 0;
    grid-area: title;
  }
}
.creations__cards {
  display: grid;
  gap: 1.5rem;
  margin-bottom: 2.125rem;
}
@media (min-width: 40rem) {
  .creations__cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 62.5rem) {
  .creations__cards {
    grid-area: cards;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.875rem;
  }
}
.creations__card {
  position: relative;
  padding: 3.25rem 1.25rem 1.25rem;
  background-size: cover;
  background-position: center;
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
}
@media (min-width: 40rem) {
  .creations__card {
    padding: 11.25rem 1.875rem 1.875rem;
  }
}
@media (min-width: 62.5rem) {
  .creations__card {
    padding: 22.125rem 1.25rem 2rem 2.5rem;
  }
}
.creations__card::after {
  content: "";
  visibility: hidden;
  background-color: rgba(255, 255, 255, 0.75071);
  position: absolute;
  inset: 0;
  z-index: 1;
}
.creations__card:hover {
  color: hsl(0deg, 0%, 0%);
}
.creations__card:hover::after {
  visibility: visible;
}
.creations__card:nth-child(1) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-deep-earth.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(1) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-deep-earth.jpg);
  }
}
.creations__card:nth-child(2) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-night-arcade.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(2) {
