}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(3) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-soccer-team.jpg);
  }
}
.creations__card:nth-child(4) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-grid.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(4) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-grid.jpg);
  }
}
.creations__card:nth-child(5) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-from-above.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(5) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-from-above.jpg);
  }
}
.creations__card:nth-child(6) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-pocket-borealis.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(6) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-pocket-borealis.jpg);
  }
}
.creations__card:nth-child(7) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-curiosity.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(7) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-curiosity.jpg);
  }
}
.creations__card:nth-child(8) {
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/mobile/image-fisheye.jpg);
}
@media (min-width: 62.5rem) {
  .creations__card:nth-child(8) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.0001) 0%, rgba(0, 0, 0, 0.601863) 100%), url(/images/desktop/image-fisheye.jpg);
  }
}
.creations__card-text {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-family: "Josefin Sans", sans-serif;
  text-align: left;
  text-transform: uppercase;
  z-index: 99;
}
@media (min-width: 40rem) {
  .creations__card-text {
    font-size: 2rem;
    line-height: 2rem;
  }
}
.creations__card-text span {
  display: block;
}
.creations__button {
  display: inline-block;
  color: hsl(0deg, 0%, 0%);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  padding: 0.8125rem 2.375rem;
  border: 0.0625rem solid hsl(0deg, 0%, 0%);
  transition: all 200ms ease-in-out;
}
@media (min-width: 62.5rem) {
  .creations__button {
    grid-area: button;
  }
}
.creations__button:hover {
  background-color: hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 100%);
}